* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.navbar {
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    padding: 0;

}

.navbar .nav-item:hover {
    background-color: #dc3545;
    border-radius: 10px;
    transition: 0.1s ease-in;
}

.nav-link:hover {
    color: white;
    transition: 0.1s ease-in;
}

.nav-link.active {
    background-color: #dc3545;
    border-radius: 10px;
    font-weight: 400;
    color: white !important;
}

.carousel-caption {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    /* padding-top: 1.25rem; */
    /* padding-bottom: 1.25rem; */
    color: #fff;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.61);
    /* width: 100vw; */
}

.pdf {
    margin: 1rem;
    text-align: center;
}

.pdfLink {
    font-size: 1.5rem;
    color: rgb(67, 65, 192);
    font-weight: 700;
}

.pdfLink:hover {
    color: rgb(104, 144, 219);
    ;
}

.news {
    max-width: calc(100vw / 3)
}

.newsHeader {
    padding: 0 1rem;
    display: flex;
    align-items: center;
}

.newsHeader .heading {
    padding: 0.5rem;
    font-weight: 700;
    border-bottom: 4px solid white;
}

.newsHeader .heading.active {
    color: #dc3545;
    transition: 0.1s ease-in;
    border-bottom: 4px solid #dc3545;
}

.newsHeader .heading:hover {
    color: #dc3545;
    transition: 0.1s ease-in;
    border-bottom: 4px solid #dc3545;
}

